.container{
    background-color: rgb(38, 89, 151);
    width: 100%;
    text-align: center;
    vertical-align: middle;
    padding: 10px;
    margin: 0 auto;
    font-family: 'Raleway', sans-serif;
}
.container .subcontainer{
    width: 900px;
    margin: 0 auto;
    height: 150px;
} 
.logo{
    color: #fff;
    font-weight: bolder;
    font-size: 80px;
    padding-top: 25px;
    width: 80%;
    margin: 0px 80px;
    vertical-align: middle;
    font-family: 'Raleway', sans-serif;
}

.segundo-subcontainer{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100px;
}

.search{
    width: 500px;
    text-align: center;
    
}

.search input{
    width: calc(500px - 20px - 10px);
    padding: 10px;
    font-size: 20px;
    border-radius: 25px;
    text-align: center;
    text-decoration: none;
    outline: none;
    border: 3px solid rgb(3, 19, 39);
    opacity: 0.9;
}
.actions{
    width: auto;

}

.actions button{
    border-radius: 9px;
    width: 250px;
    font-size: 20px;
    background-color: black;
    color: honeydew;
    height: 46px;
}

.actions ul, .actions ul li{
    margin: 0;
    list-style: none;
}
.actions ul li a{
    display: inline;
    padding: 10px;
}

.new-item-panel-container{
    background-color: #000;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: fixed;
}

.new-item-panel{
    width: 300px;
    margin: 20px auto;
    background-color: white;
    padding: 20px;
}
.new-item-panel p{
    padding: 5px 0;
}